<template>
  <div class="row">
    <div class="col-md-12">
      <h3 class="title">User Tables</h3>
    </div>
    <div class="col-md-12 card" icon>
      <div class="card-body row">
        <div class="col-sm-6">
         <span class="pull-left">
        <div class="status">
        <h4>Status:</h4>
        <div class="list-status">
          <div
            class="item"
            v-for="(status, index) in listStatus"
            :key="status"
            @click="chooseStatus(index)"
            :class="{ active: status == selectedStatus }"
          >
            {{ status }}
          </div>
        </div>
      </div>
     </span>
        </div>
        <div class="col-sm-6">
          <div class="pull-right">
            <fg-input
              class="input-sm"
              placeholder="Search name"
              v-model="searchQuery"
              addon-right-icon="nc-icon nc-zoom-split"
            >
            </fg-input>
          </div>
        </div>
        <div class="col-sm-12 mt-2">
          <el-table
            class="table-striped"
            :data="queriedData"
            border
            style="width: 100%"
          >
            <!-- <el-table-column prop="id" minWidth="120" label="id">
            </el-table-column> -->
            <el-table-column prop="username" minWidth="120" label="Name">
            </el-table-column>
            <el-table-column prop="fullname" minWidth="120" label="FullName">
            </el-table-column>
            <el-table-column prop="email" min-width="150" label="Email">
            </el-table-column>
            <el-table-column prop="phone" min-width="80" label="Phone">
            </el-table-column>
            <el-table-column prop="is_system_admin" min-width="75" label="Role">
            </el-table-column>
            <el-table-column align="center" min-width="50" label="Status">
              <template slot-scope="props">
                <p style="color: green" v-if="props.row.status">
                  &#9679; Active
                </p>
                <p style="color: red" v-else>&#9679; Inactive</p>
              </template>
            </el-table-column>

            <el-table-column
              :min-width="70"
              fixed="right"
              class-name="td-actions"
              label="Actions"
            >
              <template slot-scope="props">
                <!-- <p-button type="info" size="sm" icon @click="handleLike(props.$index, props.row)">
                  <i class="fa fa-user"></i>
                </p-button> -->
                <!-- <p-button
                  type="success"
                  size="sm"
                  icon
                  @click="handleEdit(props.$index, props.row)"
                >
                  <i class="fa fa-edit"></i>
                </p-button> -->
                <p-button
                  type="info "
                  icon
                  @click="transferToResidentPage(props.$index, props.row)"
                >
                  <i class="nc-icon nc-tap-01" />
                </p-button>
                <p-button
                  type="danger"
                  size="sm"
                  icon
                  @click="handleDelete(props.$index, props.row)"
                >
                  <i class="fa fa-times"></i>
                </p-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <div class="col-sm-6 pagination-info">
          <p class="category">
            Showing
            {{
              this.pagination.perPage * (this.pagination.currentPage - 1) + 1
            }}
            to {{ to }} of {{ total }} entries
          </p>
        </div>
        <div class="col-sm-6">
          <p-pagination
            class="pull-right"
            v-model="pagination.currentPage"
            :per-page="pagination.perPage"
            :pageCount="pagination.totalPage"
          >
          </p-pagination>
          <!-- @input="reloadData"  -->
        </div>
  </div>
</template>
<script>
  import Vue from "vue";
  import { Table, TableColumn, Select, Option } from "element-ui";
  import PPagination from "src/components/UIComponents/Pagination.vue";
  import PSwitch from "src/components/UIComponents/Switch.vue";
  import Button from "src/components/UIComponents/Button.vue";
  import axios from "axios";
  import { bus } from "@/main.js";
  //
  Vue.use(Table);
  Vue.use(TableColumn);
  Vue.use(Select);
  Vue.use(Option);
  export default {
    components: {
      PPagination,
      PSwitch,
      Button,
    },
    props: {
      userId: Number,
    },
    watch: {
      selectedStatus() {
        this.loadData();
      },
    },
    computed: {
      pagedData() {
        return this.tableData.slice(this.from, this.to);
      },
      /***
       * Searches through table data and returns a paginated array.
       * Note that this should not be used for table with a lot of data as it might be slow!
       * Do the search and the pagination on the server and display the data retrieved from server instead.
       * @returns {computed.pagedData}
       */
      queriedData() {
        if (!this.searchQuery) {
          this.pagination.total = this.tableData.length;
          return this.pagedData;
        }
        let result = this.tableData.filter((row) => {
          let isIncluded = false;
          for (let key of this.propsToSearch) {
            let rowValue = row[key].toString();
            if (rowValue.includes && rowValue.includes(this.searchQuery)) {
              isIncluded = true;
            }
          }
          return isIncluded;
        });
        this.pagination.total = result.length;
        return result.slice(this.from, this.to);
      },
      to() {
        let highBound = this.from + this.pagination.perPage;
        if (this.total < highBound) {
          highBound = this.total;
        }
        return highBound;
      },
      from() {
        return this.pagination.perPage * (this.pagination.currentPage - 1);
      },
      total() {
        this.pagination.total = this.tableData.length;
        return this.tableData.length;
      },
    },
    created() {
      const token = localStorage.getItem("token");
      this.loadData();
    },
    mounted() {
    bus.$emit("hide", false);
  },
  beforeDestroy() {
    // bus.$off("apartmentId", this.getApartmentId);
    bus.$emit("show", true);
  },
    data() {
      return {
        pagination: {
          perPage: 10,
          currentPage: 1,
          perPageOptions: [5, 10, 25, 50],
          total: 0,
          totalPage: 1,
        },
        // perFilterOptions: "All user",
        // filterOptions: ["System Admin", "Group Admin", "Resident"],
        searchQuery: "",
        propsToSearch: ["username", "fullname", "email", "phone"],
        tableData: [],
        listStatus: ["All", "Active", "Inactive"],
        selectedStatus: "All",
      };
    },
    methods: {
      transferToResidentPage(index, row) {
        this.userId = row.id;
        this.$emit("passStoreID", this.userId);
        console.log("id:", this.userId);
        this.$router.push({
          name: "Resident",
          params: { userId: row.id },
        });
      },
      async loadData() {
        const token = localStorage.getItem("token");
        let res = await axios.get("http://18.136.195.171:8080/api/v1/users", {
          headers: {
            Authorization: "Bearer " + token,
          },
        });
        if (res.status == 200 && res.data != "") {
          this.tableData = res.data.items;
          console.log("table data", this.tableData);
          if (this.selectedStatus == "All") {
            this.tableData = res.data.items;
          } else {
            let statusCheck = true;
            if (this.selectedStatus == "Inactive") {
              statusCheck = false;
            }
            let tmpData = [];
            this.tableData.forEach((element) => {
              if (element.status == statusCheck) {
                tmpData.push(element);
              }
            });
            this.tableData = tmpData;
          }
        }
      },
      chooseStatus(index) {
        this.selectedStatus = this.listStatus[index];
        this.loadData();
      },
      handleEdit(index, row) {
        alert(`Your want to edit ${row.name}`);
      },
      handleDelete(index, row) {
        let confirmation = confirm("Do you want to Inactive? ");
        if (confirmation) {
          let indexToDelete = this.tableData.findIndex(
            (tableRow) => tableRow.id === row.id
          );
          if (indexToDelete >= 0) {
            const token = localStorage.getItem("token");
            let res = axios
              .put(
                "http://18.136.195.171:8080/api/v1/users/{id}",
                {
                  id: row.id,
                  username: row.username,
                  password: row.password,
                  fullname: row.fullname,
                  phone: row.phone,
                  email: row.email,
                  is_system_admin: row.is_system_admin,
                  status: false,
                },
                {
                  headers: {
                    Authorization: "Bearer " + token,
                  },
                }
              )
              .then((this.tableData[indexToDelete].status = false));
          }
        }
      },
    },
  };
</script>
<style lang="scss">
.el-table .td-actions {
  button.btn {
    margin-right: 5px;
  }
}
p {
  margin: 0;
}
.left {
  display: flex;
  margin-bottom: 10px;
}
h4 {
  padding-right: 20px;
  font-size: 18px;
  height: fit-content;
}
.select-default,
h4 {
  margin: auto 0;
}
.el-table .td-actions {
  button.btn {
    margin-right: 5px;
  }
}
.pull-left {
  height: 100%;
  display: flex;
}
.status {
  justify-content: center;
}
.status,
.list-status {
  margin: auto 10px;
  display: flex;
}
.list-status {
  width: 180px;
  justify-content: space-between;
}
.item {
  cursor: pointer;
  user-select: none;
  font-size: 15px;
}
.item.active {
  color: rgb(250, 102, 16);
  border-bottom: 2px solid rgb(250, 102, 16);
}
</style>